<template>
    <div class="myAddress">
      <van-nav-bar
  title="收货地址"
  left-text="返回"
  left-arrow
  @click-left="$router.go(-1)"
/>

        <van-address-list
  v-model="AddressId"
  :list="addresslist"
  default-tag-text="默认"
  @add="moveToAddress"
  @edit="moveToAddress"
/>
    </div>
</template>

<script>
   export default {
  data() {
    return {
      AddressId: '1',
      addresslist: [
      ], 
    };
  },
  created() {
    this.getAddress()
  },
  methods: {
    //获取当前添加或者更改的地址数据
    getAddress(){
       let tokenString = localStorage.getItem('__jigng');
       this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'get',
        url:'/findAddress',
        params:{
          appkey:this.appkey,
          tokenString,
        }
      }).then(res=>{
        this.$toast.clear()
       
       if(res.data.code==20000){
         res.data.result.forEach(v=>{
           let addressobj={
              id: v.aid,
              tel: v.tel,
              name: v.name,
              address: v.province + v.city + v.county + v.addressDetail,
              //转成布尔值判断是否默认
              isDefault: Boolean(v.isDefault)
           }
           
           
            //默认选中
           if(v.isDefault){
             //赋值
             this.aid=v.aid
             
           }
           this.addresslist.push(addressobj)
           
         })
       }
      }).catch(err=>{
         this.$toast.clear()
        
      })

    },
    moveToAddress(address){
      let name={
        name:'修改地址'
      }
      //有参数则点击了编辑
      if(address){
        name.params={
          aid:address.id
        }
      }
      
      
      this.$router.push(name)
    }
  },
};
</script>

<style lang="less" scoped>
.myAddress{
  
  /deep/ .van-radio__icon{
    display: none;
  }
}

</style>